import React, { useEffect } from "react";
import { useNavigate } from "react-router-dom";

const First = () => {
  const navigate = useNavigate();

  useEffect(() => {
    setTimeout(() => {
      showCanvas();
    }, 1000);
  }, []);

  function showCanvas() {
    const canvas = document.getElementById("myCanvas");
    const ctx = canvas.getContext("2d");

    function drawTriangle(x, y) {
      ctx.fillStyle = "green";

      ctx.beginPath();

      ctx.moveTo(x, y);

      ctx.lineTo(x + 100, y - 100);

      ctx.lineTo(x + 200, y);

      ctx.fill();
    }

    var backgroundLinear = ctx.createLinearGradient(0, 0, 0, 350);

    backgroundLinear.addColorStop(0, "#151c2e");

    backgroundLinear.addColorStop(0.9, "white");

    backgroundLinear.addColorStop(1, "green");

    ctx.fillStyle = backgroundLinear;

    ctx.fillRect(0, 0, 500, 450);

    drawTriangle(30, 120); //red triangle

    drawTriangle(30, 190); //blue triangle

    drawTriangle(30, 260); //green triangle

    ctx.fillStyle = "brown";
    ctx.fillRect(110, 260, 40, 100);

    var y = 40;

    ctx.beginPath();
    ctx.fillStyle = "yellow";
    ctx.arc(450, y, 20, 0, 2 * Math.PI);
    ctx.fill();
  }

  return (
    <div>
      <div id="header">
        <canvas id="myCanvas" width="1000" height="400"></canvas>
      </div>
      <button
        className="bg-gradient-to-r mt-4 from-cyan-500 to-teal-500 text-white px-4 py-2 rounded-md ml-8"
        onClick={() => navigate("/")}
      >
        Back
      </button>
    </div>
  );
};

export default First;
